import APIUtil from './api_util';
import FollowToggle from './follow_toggle';

class UsersSearch {
  constructor(el) {
    this.$el = $(el);
    this.$input = this.$el.find("input[name=username]");
    this.$ul = this.$el.find(".users");

    this.$el.find(".simple-form").submit(() => false);
    this.$input.on("input", _.debounce(this.handleInput.bind(this), 275));
  }

  // spaces do not affect query ouput
  handleInput(e) {
    let query = this.$input.val();

    query = query.replace(/\s+/g, "");

    if (!query) {
      this.$ul.html("");

    } else {
      if (e.originalEvent.data !== " ") {
        APIUtil.searchUsers(query).then((users) =>
          this.render(users)
        );
      }
    }
  }

  render(users) {
    this.$ul.empty();

    if (users.length === 0) {
      this.$ul.html("No results matched your search");
      this.$ul.addClass("no-results");
    } else {
      this.$ul.removeClass("no-results");
    }

    for (let i = 0; i < users.length; i++) {
      const user = users[i];

      let $a = $("<a></a>");
      $a.text(`@${user.username}`);
      $a.attr("href", `/users/${user.id}`);

      const $li = $("<li></li>");
      $li.append($a);

      if (current_user) {
        const $followToggle = $("<button></button>");

        if (user.id !== current_user) {
          new FollowToggle($followToggle, {
            userId: user.id,
            followState: user.followed ? "followed" : "unfollowed"
          });
        } else {
          new FollowToggle($followToggle, {
            userId: user.id,
            followState: "this is you!"
          });
        }

        $li.append($followToggle);
      }

      this.$ul.append($li);
    }
  }
}

export default UsersSearch;
